@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Goldman&family=Poppins:wght@200&display=swap');


* {
    margin: 0;
    font-family: 'Poppins', sans-serif;
    line-height: 1.9;
}

.login-logo {
    padding: 20px 0px 0px 20px;
}

.login-logo img {
    height: 40px;
    width: auto;
}

p {
    color: #01151D;
    font-size: 14px;
}

.Error_cnt {
    padding: 16px;
    background-color: #F8D7DA;
    border-radius: 5px;
    border: 1px solid #F1AEB5;
    list-style-type: none;
}

.Error_cnt li {
    color: #58151C;
    margin: 0px;
}

.Success_cnt {
    padding: 16px;
    background-color: #D1E7DD;
    border-radius: 5px;
    border: 1px solid #A3CFBB;
    list-style-type: none;
}

.Success_cnt .Msg {
    color: #0A3622;
    margin: 0px;
}

.showpass {
    position: absolute;
    min-width: 50px;
    margin-top: 28px;
}

.signupicon {
    margin-top: -30px;
}

.passwordCnt {
    display: block;
    width: auto !important;
    padding: 0px;
    border: 0;
    outline: 0;
    border-radius: 5px;
    background-color: transparent;
    position: relative !important;
}

.main_items {
    margin: 30px auto;
    width: 90%;
}

.btn-primary {
    background-color: #01151D !important;
}

.btn-white {
    background-color: white !important;
    color: #01151D !important;
    outline: 0 !important;
    border: 0 !important;
}

.twoRows {
    display: grid;
    grid-template-columns: repeat(2, minmax(80px, 1fr));
    gap: 20px;
    margin-bottom: 10px;
}

.first_cnt {
    width: 50%;
    padding: 20px;
}

.signup-container {
    max-width: 1536px;
    padding: 10px;
    margin: 0px auto;
}

.form_container {
    padding: 20px;
    display: grid;
    grid-auto-flow: row;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
    grid-auto-rows: minmax(0px, auto);
}

.right p {
    color: #ACCEDB;
    font-size: 13px;
}

.bottomItem p {
    color: darkblue;
}

.bottomItem {
    color: darkblue;
    position: absolute;
    justify-content: space-between;
    display: flex;
    bottom: 0;
    width: 100%;
}

.leftitem {
    font-weight: bold;
}

.btnContainer {
    width: 90%;
    padding: 5px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.rightitem {
    font-weight: bold;
}

.right {
    width: 90%;
    padding: 20px;
    background-color: #05678D;
}

.left {
    position: relative;
    padding: 20px;
}

.forgetPassword a {
    display: inline-block;
    color: #FDB3B2;
    font-weight: bold;
    text-align: right;
}

.icon {
    font-size: 20px;
    margin-right: 5px;
    margin-left: -10px;
}

.passwordCnt {
    position: relative;
    width: 100%;
}

.showpass {
    position: absolute;
    right: 0;
    top: 20px;
}

.right .btn-Container {
    margin-top: 10px !important;
    display: flex;
    flex-direction: column;
    height: auto;
    justify-content: space-between;
    align-items: center;
}

.input {
    font-size: 13px !important;
    display: block;
    width: 100%;
    padding: 5px 10px !important;
    border: 0;
    outline: 0;
    border-radius: 5px;
}

.heading {
    font-size: 48px;
    font-family: 'Goldman', cursive;
}

.displayText {
    font-size: 64px;
    font-family: 'Goldman', cursive;
}

.btn-darkblue {
    background-color: #01151D;
    color: #E6F0F3;
}

.right .heading2 {
    font-size: 40px;
    font-family: 'Goldman', cursive;
    color: #ACCEDB !important;
}

label {
    font-weight: bold;
    color: #E6F0F3;

}

.displayButton {
    font-size: 20px;
    font-family: 'Goldman', cursive;
}


body {
    font-size: 16px
}

.signupImg {
    display: flex;
    justify-content: center;
    align-items: center;
}

.signupImg img {
    width: 80%;
    height: auto;
    object-fit: contain;
}

.btn {
    font-size: 16px;
    display: block;
    border: 0;
    padding: 10px 5px;
    margin-bottom: 10px;
    width: 248px;
    border-radius: 5px;

}

.FlexDisplay {
    display: flex;
    align-items: center;
    padding: 5px;
}

.FlexDisplay p {
    margin-left: 5px;
    margin-top: 15px !important;
}

.FlexDisplay .policy {
    color: #01151D;
    font-weight: bold;
}

.dontHaveAccount {
    text-align: center;
    color: #ACCEDB;
}

.dontHaveAccount a {
    color: #3da1c7;
    font-weight: bold;
}

.bottomItem::after {
    content: "";
    display: table;
    clear: both;
}

p {
    font-size: 14px;
    color: #E6F0F3
}

/* mobile view design*/


@media screen and (max-width: 768px) {

    .form_container {
        display: flex;
        flex-direction: column;
        padding: 5px;
        align-items: center;
    }

    .right {
        display: flex;
        max-width: 500px;
        width: 100%;
        padding: 5px;
    }

    .input {
        font-size: 13px !important;
        display: block;
        width: 100% !important;
        padding: 5px 10px !important;
    }

    .imgCenter {
        display: none;
    }

    .bottomItem {
        display: none;
    }

    .left {
        display: none
    }

    .right .btn-Container {
        margin-top: 0 !important;
        display: block;
    }

    .heading2 {
        font-size: 25px !important;
    }

    .twoRows {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-bottom: 10px;
        width: 100%;
    }

    .right .btn-Container {
        margin-top: 10px !important;
        display: flex;
        flex-direction: column;
        height: auto;
        justify-content: center;
        align-items: center;
    }
}